import { AnalysisResult } from '@/types';

/**
 * Generates a fake Grad-CAM heatmap as a base64 data URL.
 * @param isMetastatic Determines if a "hot" area should be drawn.
 * @returns A data URL string for a PNG image.
 */
const generateFakeHeatmap = (isMetastatic: boolean): string => {
  const canvas = document.createElement('canvas');
  const size = 96; // Matching PCam patch size for realism
  canvas.width = size;
  canvas.height = size;
  const ctx = canvas.getContext('2d');

  if (!ctx) {
    console.error("Could not get 2D context from canvas");
    return '';
  }

  // Only draw a heatmap if the prediction is metastatic
  if (isMetastatic) {
    // Create a "hot" spot for metastatic prediction at a random-ish location
    const centerX = size * (0.4 + Math.random() * 0.2);
    const centerY = size * (0.4 + Math.random() * 0.2);
    const radius = size * (0.3 + Math.random() * 0.15);

    const gradient = ctx.createRadialGradient(centerX, centerY, 0, centerX, centerY, radius);
    gradient.addColorStop(0, 'rgba(255, 0, 0, 0.7)');       // Red center
    gradient.addColorStop(0.5, 'rgba(255, 255, 0, 0.5)');   // Yellow mid
    gradient.addColorStop(1, 'rgba(0, 255, 255, 0)');      // Transparent outer

    ctx.fillStyle = gradient;
    ctx.fillRect(0, 0, size, size);
  }
  
  // For non-metastatic cases, an empty (transparent) canvas is returned,
  // which won't show an overlay.
  return canvas.toDataURL();
};


/**
 * Mocks the backend analysis of a histopathology image patch.
 * In a real application, this would be an API call to a server running a
 * TensorFlow/PyTorch model.
 *
 * @param base64Image The base64 encoded image string.
 * @returns A promise that resolves to an AnalysisResult.
 */
export const analyzeImage = (base64Image: string): Promise<AnalysisResult> => {
  return new Promise((resolve) => {
    // Simulate network latency and model inference time
    setTimeout(() => {
      // Randomly generate a result for demonstration purposes
      const isMetastatic = Math.random() > 0.5;
      const confidence = 0.75 + Math.random() * 0.24; // Confidence between 75% and 99%

      // Generate a placeholder heatmap.
      const heatmapUrl = generateFakeHeatmap(isMetastatic);

      const result: AnalysisResult = {
        prediction: isMetastatic ? 'Metastatic' : 'Non-Metastatic',
        confidence: confidence,
        heatmapUrl: heatmapUrl,
      };

      resolve(result);
    }, 1500 + Math.random() * 1000); // Simulate 1.5 to 2.5 seconds processing time
  });
};