"use client"; 

import React, { useState, useCallback } from 'react';
import { Header } from '@/components/Header';
import { ImageAnalyzer } from '@/components/ImageAnalyzer';
import { Chatbot } from '@/components/Chatbot';
import { AnalysisResult, ChatMessage } from '@/types';

const Home: React.FC = () => {
  const [analysisResult, setAnalysisResult] = useState<AnalysisResult | null>(null);
  const [chatMessages, setChatMessages] = useState<ChatMessage[]>([]);
  const [isChatLoading, setIsChatLoading] = useState(false);

  const handleAnalysisComplete = useCallback((result: AnalysisResult) => {
    setAnalysisResult(result);
    // Add a system message to the chat when analysis is done
    setChatMessages(prevMessages => [
        ...prevMessages,
        {
            role: 'model',
            parts: [{ text: `Analysis complete. The model predicts: **${result.prediction}** with ${Math.round(result.confidence * 100)}% confidence. Feel free to ask me any questions about this result or general histopathology.` }]
        }
    ]);
  }, []);

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="grow p-4 sm:p-6 lg:p-8 overflow-hidden">
        <div className="max-w-7xl mx-auto flex flex-col lg:flex-row gap-8 h-full">
          <div className="lg:w-1/2">
            <h2 className="text-2xl font-bold mb-4 text-gray-800 dark:text-gray-200">Image Analysis</h2>
            <ImageAnalyzer onAnalysisComplete={handleAnalysisComplete} />
          </div>
          <div className="lg:w-1/2 flex flex-col">
            <h2 className="text-2xl font-bold mb-4 text-gray-800 dark:text-gray-200">Chat Assistant</h2>
            <Chatbot
              analysisResult={analysisResult}
              messages={chatMessages}
              setMessages={setChatMessages}
              isLoading={isChatLoading}
              setIsLoading={setIsChatLoading}
            />
          </div>
        </div>
      </main>
      <footer className="text-center p-4 text-gray-500 dark:text-gray-400 text-sm">
        <p>&copy; 2024 Histopath. AI for medical imaging.</p>
      </footer>
    </div>
  );
};

export default Home;